// client/src/pages/Cart.js
import React from 'react';
import {
  Container,
  Typography,
  List,
  ListItem,
  ListItemText,
  IconButton,
  TextField,
  Button,
  Paper,
  Box,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import { useCart } from '../context/CartContext';
import { useNavigate } from 'react-router-dom';

const Cart = () => {
  const { cart, dispatch } = useCart();
  const navigate = useNavigate();

  const handleRemove = (id) => {
    dispatch({ type: 'REMOVE_FROM_CART', payload: id });
  };

  const handleQtyChange = (id, quantity) => {
    const qty = Math.max(1, parseInt(quantity));
    dispatch({ type: 'UPDATE_QUANTITY', payload: { id, quantity: qty } });
  };

  const handlePlaceOrder = () => {
    const orders = JSON.parse(localStorage.getItem('orders')) || [];
    const newOrder = {
      id: Date.now(),
      items: cart,
      total: cart.reduce((sum, item) => sum + item.price * item.quantity, 0),
      date: new Date().toLocaleString(),
    };
    localStorage.setItem('orders', JSON.stringify([...orders, newOrder]));
    dispatch({ type: 'CLEAR_CART' });
    navigate('/order-summary');
  };

  const total = cart.reduce((sum, item) => sum + item.price * item.quantity, 0);

  return (
    <Container
      sx={{
        mt: 4,
        bgcolor: 'white',
        p: 3,
        borderRadius: 2,
        boxShadow: 3,
        minHeight: '70vh',
      }}
    >
      <Box display="flex" alignItems="center" mb={3}>
        <ShoppingCartIcon sx={{ color: 'red', fontSize: 35, mr: 1 }} />
        <Typography variant="h4" color="orange">
          Your Cart
        </Typography>
      </Box>

      {cart.length === 0 ? (
        <Typography sx={{ mt: 2 }} color="text.secondary">
          Your cart is empty.
        </Typography>
      ) : (
        <>
          <List>
            {cart.map((item) => (
              <Paper
                elevation={2}
                sx={{ mb: 2, p: 2, borderLeft: '5px solid orange' }}
                key={item.id}
              >
                <ListItem
                  alignItems="flex-start"
                  secondaryAction={
                    <Box display="flex" alignItems="center">
                      <TextField
                        type="number"
                        size="small"
                        value={item.quantity}
                        onChange={(e) => handleQtyChange(item.id, e.target.value)}
                        sx={{ width: 60, mr: 2 }}
                        inputProps={{ min: 1 }}
                      />
                      <IconButton onClick={() => handleRemove(item.id)} sx={{ color: 'red' }}>
                        <DeleteIcon />
                      </IconButton>
                    </Box>
                  }
                >
                  <ListItemText
                    primary={
                      <Typography fontWeight="bold" color="goldenrod">
                        {item.name}
                      </Typography>
                    }
                    secondary={`R${item.price.toFixed(2)} x ${item.quantity}`}
                  />
                </ListItem>
              </Paper>
            ))}
          </List>

          <Typography variant="h6" sx={{ mt: 2 }} color="orange">
            Total:{' '}
            <Typography component="span" fontWeight="bold" color="goldenrod">
              R{total.toFixed(2)}
            </Typography>
          </Typography>

          <Button
            variant="contained"
            fullWidth
            sx={{
              mt: 3,
              backgroundColor: 'red',
              '&:hover': { backgroundColor: '#b30000' },
              color: 'white',
              fontWeight: 'bold',
            }}
            onClick={handlePlaceOrder}
          >
            Place Order
          </Button>
        </>
      )}
    </Container>
  );
};

export default Cart;
